.container{
 min-width: 100%;
 min-height: 100vh; /* will cover the 100% of viewport */
 overflow: hidden;
 display: block;
 position: relative;
 padding-bottom: 12rem; /* height of your footer */
 background-color: #fcfcfc;
 padding-right: 0;
 padding-left: 0;
 font-family: TimesNewRoman,Times New Roman,Times,Baskerville,Georgia,serif;
}

.App {
  position: relative;
  min-height: 100vh;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  min-height: 5vh;
  min-width: 97vw;
  display: flex;
  //flex-direction: column;
  //align-items: flex-end;
  //justify-content: flex-end;
  font-size: 2vmin;
  //color: grey;
  //background-color: white
}

.App-link {
  display:flex;
  flex-direction:row;
  align-items: flex-start;
  justify-content: flex-end;
  //color: white;
  background:none;
  color:inherit;
  //display: block;
  //align-items: right;
  border:none;
  //font: inherit;
  padding: 0px 30px;
  /*border is optional*/

  cursor: pointer;
}

.center-text{
  text-align: center;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Content-wrap {
  padding-bottom: 12rem;    /* Footer height */
  background-color: grey
}

.Toolbar {


}

.Name_and_Logo{
  //color: #61dafb;
  background:none;
  color:inherit;
  //display: block;
  //align-items: right;
  border:none;

  text-decoration: inherit;
}

.Header-Link{
  padding: 0px 30px;
  color: grey;
  text-decoration: inherit;
  text-align: right;
}

.Logo{
  float: left;
}
.Name{
  text-align: left;
  float: right;
  color: grey;
  padding-top: 8px;
}

.home-carousel{
  max-height: 1vh;
}

.Lead-text{
  text-align:center;
}

.black-text{
  color: black;
}

img.floatLeft{
  float: left;
}


.Foot {
  position: absolute;
  bottom: 0;
  width: 99vw;
  height: 12rem;            /* Footer height */
  background-color: #e6edf7;

  font-size: 12px;
}


.Footer-Link{
  padding-bottom: 1px;
  color: black;
  text-decoration: inherit;
  text-align: center;
  font-size: 12px
}

.signature{
  background-color: #d8dfeb;
  text-align: right;
  justify-content: flex-end;
  padding-right: 5px;
  position: absolute;
  bottom: 0;
  width:99vw;
}

.shrink{
  color:yellow;
  max-height: 30vw;
  position:relative;
}

.indent{
  padding-left: 30px;
}

.align-bottom{
  justify-content: flex-end;
}

.carousel-item img{
  filter:brightness(50%)
}
